@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: white;
}



  
.container{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: flex-start;
    
}

.gmail {
    margin-right: 25px;
}

.gmail a {
    text-decoration: none;
    color: black;
    font-size: 10px;
}

.gmail a:hover {
    text-decoration: underline;
}

.images {
    margin-right: 25px;
   
}

.images  a {
    
    text-decoration: none;
    color: black;
    font-size: 10px;
}

.images  a:hover {
    text-decoration: underline;
}

img {
    width: 20px;
    height: 20px;
    margin-top: 6px;
    margin-right: 25px;
}

.btn {
    cursor: pointer;
    padding: 5px 5px;
    background-color: rgb(164, 164, 201);
}

.btn:hover {
    background-color: rgb(103, 103, 146);
}
.contain {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 80vh;
}

.contain input {
  background-image: url("https://cdn0.iconfinder.com/data/icons/very-basic-2-android-l-lollipop-icon-pack/24/search-512.png");
  background-size: contain;
  background-repeat: no-repeat;
  text-indent: 20px;
  padding: 5px 3px;
  transition: 0.3s;
  width: 500px;
  border-color: rgb(221, 221, 221);
  border-width: 1px;
  border-style: solid;
  border-radius: 15px;
  outline: none;
}

.contain input:focus {
  background-image: none;
  text-indent: 0px;
}

.contain input:hover {
    box-shadow: 1px 1px 9px 0px gray;
  }
.contain h1 {
    background: repeating-linear-gradient(
        90deg,
        blue 0px,
        red 40px,
        yellow 40px,
        green 80px
      );

      -webkit-background-clip: text;

              background-clip: text;
      color: transparent;
}

.contain .one {
    margin-right: 25px;

}

.contain img {
    width: 250px;
    height:50px
}

.contain button {
    font-size: 9px;
}

.contain .Button {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin: 20px 0px 0px 0px;
}

.contain button:hover {
-webkit-transform: scale(1.1);
        transform: scale(1.1);
box-shadow: 1px 1px 6px 0px grey;
}

a {
    text-decoration: none;
    margin-right: 5px;
}

.contain p {
    font-size: 10px;
}
